.Profiles{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    gap: 5rem 0;
}

.Profiles h1{
    font-size: 5rem;
    text-align: center;
}

.Profiles__prof{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    padding: 1rem;
    box-sizing: border-box;
}

.Profiles button{
    margin-top: 3rem;
    width: 25rem;
    height: auto;
    padding: 1.5rem 2rem;
    box-sizing: border-box;
    background-color: transparent;
    color: var(--color-grey);
    font-size: 2rem;
    border: 1px solid var(--color-grey);
    cursor: pointer;
}

.Profiles button:hover{
    color: var(--color-text);
    border-color: var(--color-text);
}

.Profiles__profUser{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 0 5rem;
    width: 100%;
    flex-wrap: wrap;
}

.Profiles__profUser div{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    box-sizing: border-box;
    gap: 2rem 0;
    cursor: pointer;
}

.Profiles__profUser div:hover .Profiles__profImg{
    outline: 2px solid var(--color-text);
}

.Profiles__profUser div:hover h2{
    color: var(--color-text);
}


.Profiles__profUser h2{
    color: var(--color-grey);
}

.Profiles__profImg{
    width: 20rem;
    height: 20rem;
    border-radius: 10px;
    object-fit: cover;
    border: none;
    background-color: transparent;
}

@media screen and (max-width: 1440px) {
    .Profiles__profImg{
        width: 15rem;
        height: 15rem;
    }
}

@media screen and (max-width: 1024px) {
    .Profiles h1{
        font-size: 4rem;
    }
    .Profiles__profImg{
        width: 10rem;
        height: 10rem;
    }
}

@media screen and (max-width: 768px) {
    .Profiles h1{
        font-size: 3rem;
    }

    .Profiles__profImg{
        width: 8rem;
        height: 8rem;
    }
    .Profiles button{
        width: 25rem;
    }
}


@media screen and (max-width: 425px) {
    .Profiles h1{
        font-size: 2.5rem;
    }

    .Profiles button{
        width: 20rem;
    }

    .Profiles__profImg{
        width: 5rem;
        height: 5rem;
    }
}

@media screen and (max-width: 375px) {

    .Profiles h1{
        font-size: 2rem;
    }

}